import React, { Component } from 'react'
import { findDOMNode } from 'react-dom'
import gm from '../img/gm.mp4'
import glitchmob from '../img/glitchmob.gif'
import logo from '../img/logo.gif'
import 'bulma/css/bulma.css'
import '../App.css'
import Particles from 'react-particles-js';
import Countdown from 'react-countdown-now';
import { Link } from 'react-router-dom'
import { slide as Menu } from 'react-burger-menu'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'


class Home extends Component {
    constructor(props){
        super(props)
        this.setSize = this.setSize.bind(this)
    }

    state = {
        width: 0,
        height: 0,
    }

    componentDidMount() {
        this.setSize()
        window.addEventListener('resize', this.setSize)
    }

    componentDidUpdate(prevProps, prevState) {
        this.setSize()
    }

    componentWillUnmount() {
         window.removeEventListener('resize', this.setSize)
    }

    setSize(){
        // this.setState({width: window.innerWidth, height: window.innerHeight})
    }

    render(){
        let imageUrl = './img/glitchmob.gif'
        return(
            <section className="hero is-fullheight bg-img">
                <Menu>
                    <a id="home" className="menu-item" href="/">Home</a>
                    <a id="about" className="menu-item" href="spotify:playlist:49SQLBRzC3fMImzY4eRHlv"> Playlist </a>
                    <a id="contact" className="menu-item" target="_blank" href="https://drive.google.com/drive/folders/1rXQIEQuZCpKWfbI6Zlm96Zg5fLUPwKtv?usp=sharing"> Docs </a>
                 </Menu>
                 <div className="center has-text-centered">
                    <img src={logo} alt="" width="400" height="400"/>
                </div>
            </section>
        )
        return (
        <div>
            <video muted autoPlay loop id="videobg">
                <source src={gm} type="video/mp4"/>
            </video>
            <div className="content">
                <div className="topleft">
                    <a className="has-text-white" target href="javascript:;"> Schedule </a>
                </div>
                <div className="topcenter has-text-centered">
                    <img src={logo} alt="" width="400" height="400"/>
                </div>
                <div className="topright">
                    <a className="has-text-white" target="_blank" href="spotify:playlist:49SQLBRzC3fMImzY4eRHlv"> Playlist </a>
                </div>
                <div className="middleleft">
                    <a className="has-text-white" target href="javascript:;"> Attendees </a>
                </div>
                <div className="middleright">
                    <a className="has-text-white" target href="javascript:;"> </a>
                </div>
                <div className="bottomleft">
                    <a className="has-text-white" target href="javascript:;"> Videos </a>
                </div>
                <div className="bottomright">
                    <a className="has-text-white" target="_blank" href="https://drive.google.com/drive/folders/1rXQIEQuZCpKWfbI6Zlm96Zg5fLUPwKtv?usp=sharing"> Docs </a>
                </div>
            </div>

        </div>

    )}
    
}

export default Home



